import './two-factor-check.scss';
import React from 'react';
import { 
    Title,
    Text,
    Button,
    FormGroup,
    Checkbox,
    ActionGroup 
} from '@patternfly/react-core';
import ExternalLinkSquareAltIcon from '@patternfly/react-icons/dist/esm/icons/external-link-alt-icon';
import QuestionIcon from '@patternfly/react-icons/dist/esm/icons/outlined-question-circle-icon'

const statusURL = "http://localhost:8081/auth/realms/redhat-external/apis/organization/v1/my/two-factor";
const checkEndpoint = statusURL + "/enforce";
const uncheckEndpoint = statusURL + "/unenforce";

class TwoFactorCheck extends React.Component{

    constructor (props) {
        super(props);

        this.state = {
            checked: false,
            loading: true
        }
    }

    componentDidMount () {
        // Need to establish the existing state by querying the base endpoint. 
        insights.chrome.auth.getToken().then(token =>{
            fetch(statusURL,{
              method: 'GET',
              referrerPolicy: 'no-referrer',
              headers:{
                'Content-Type': 'application/json',
                'Authorization': 'Bearer ' + token
              }
          }).then(response => response.json())
            .then(data => this.setState({
                checked: data.enforced,
                loading: false
            }));
          });
    }

    updateStatus = (event) => {
        let targetURL;
        this.state.checked? targetURL=uncheckEndpoint:targetURL=checkEndpoint;
        insights.chrome.auth.getToken().then(token =>{
            fetch(targetURL,{
              method: 'POST',
              referrerPolicy: 'no-referrer',
              headers:{
                'Content-Type': 'application/json',
                'Authorization': 'Bearer ' + token
              }
          }).then(response => response.json())
            .then(data => this.setState({
                checked: data.enforced,
                loading: false
            }));
          });
    }

    learnMoreLink(){
        window.location.href = "https://access.redhat.com/documentation/en-us/red_hat_customer_portal/1/html/using_two-factor_authentication/con-ciam-2fa-about_two-factor-authentication";
    }

    render () {
        return(
            <div>
                <Title headingLevel="h2" size="xl">Organizational two-factor authentication</Title>
                
                <Text className="pf-u-w-75">
                    Enabling two-factor authentication will require all users in your 
                    organization to use their password plus a one-time code to log in to their account. 
                    In order to generate one-time codes, 
                    all users must be able to add an authenticator application to their mobile device. 
                    <Button onClick={this.learnMoreLink} variant="link" component="span" isInline icon={<ExternalLinkSquareAltIcon />} iconPosition="right">
                        Learn more about two-factor authentication
                    </Button>
                </Text>
                
                <FormGroup>
                    <Checkbox label="Require two-factor authentication for your organization" id="confirmed" className="twofaCheck" isChecked={this.state.checked}></Checkbox>
                    <Button variant="link" component="span" isInline className="questionIcon pf-u-ml-xs" icon={<QuestionIcon/>}></Button>
                </FormGroup>
                
                <ActionGroup>
                    <Button onClick={this.updateStatus} isDisabled={this.state.loading}>Save</Button>
                    <Button variant="link" isInline className="pf-u-ml-lg">Cancel</Button>
                </ActionGroup>
            </div>
        );
    }
    
}
export default TwoFactorCheck;