import React, { Suspense, lazy, useEffect } from 'react';
import { withRouter, Link } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import {
  Form,
  PageSection,
  Divider
  } from '@patternfly/react-core'
import TwoFactorCheck from '../../Components/twoFactorCheck/two-factor-check';
import { Main } from '@redhat-cloud-services/frontend-components/Main';
import {
  PageHeader,
  PageHeaderTitle,
} from '@redhat-cloud-services/frontend-components/PageHeader';
import { addNotification } from '@redhat-cloud-services/frontend-components-notifications/redux';


const targetAddress = "http://localhost:8081/auth/realms/redhat-external/apis/organization/v1/my/two-factor";


import './AuthPolicy.scss';
import { checkPropTypes } from 'prop-types';

/**
 * A smart component that handles all the api calls and data needed by the dumb components.
 * Smart components are usually classes.
 *
 * https://reactjs.org/docs/components-and-props.html
 * https://medium.com/@thejasonfile/dumb-components-and-smart-components-e7b33a698d43
 */
const AuthPolicy = () => {
  const dispatch = useDispatch();

  useEffect(() => {
    insights?.chrome?.appAction?.('sample-page');
  }, []);
  

  const handleAlert = () => {
    dispatch(
      addNotification({
        variant: 'success',
        title: 'Notification title',
        description: 'notification description',
      })
    );
  };
  
  const handleButtonClick = function (e) {
    console.log("button clicked");
    insights.chrome.auth.getToken().then(token =>{
      fetch(targetAddress,{
        method: 'GET',
        referrerPolicy: 'no-referrer',
        headers:{
          'Content-Type': 'application/json',
          'Authorization': 'Bearer ' + token
        }
    }).then(response => response.json())
      .then(data => console.log("we have returned! here is the data: " + data));
    });
  }

  return (
    <React.Fragment>
      
      <PageHeader>
        <PageHeaderTitle title="Authentication Factors" className="pf-u-mb-lg" />
        <p> Manage authentication factors for your organization </p>
      </PageHeader>
      <Divider/>
      <PageSection>
        <Form>
          <TwoFactorCheck/>
        </Form>
      </PageSection>

    </React.Fragment>
  );
};

export default withRouter(AuthPolicy);
